import {Component} from 'react';
import './Projeto.css';

export default function Projeto(){
    return(
        <>
            <section className="projeto_main">
                <h1>O Projeto</h1>
                <p className="p_normal">O projeto EBA Contempornea teve incio em 2016, 
                    a partir das comemoraes dos 200 anos da Escola de Belas Artes 
                    da UFRJ. Na poca, seu ttulo era "Contagem regressiva para os 200, 
                    comeando ao contrrio" e j propunha a realizao de um mapeamento 
                    de artistas visuais formados pela Escola, alm de algumas iniciativas 
                    coletivas, envolvendo vnculos com o que se nomeou "histria recente 
                    da EBA", desde seu deslocamento para a Cidade Universitria, na Ilha 
                    do Fundo, em 1975, at o dias atuais, de modo a dar maior 
                    visibilidade  produo de arte contempornea, na e a partir da Escola.</p>
                
                <br/>
            <div className="text_img">
                <p className="p_img">Com o desenvolvimento do projeto, frente a inmeras
                    dificuldades encontradas desde ento - como o
                    incndio ocorrido no prdio da Reitoria e a pandemia
                    de COVID-19 - optou-se pela reconfigurao do escopo
                    inicial da pesquisa, que agora divide-se em uma
                    primeira etapa, centrada na concepo deste site, um
                    banco de dados da pesquisa disponvel para ampla
                    consulta, e em uma segunda, correspondendo a
                    entrevistas com artistas, a serem realizadas no
                    futuro, de modo a constituir novas fontes de pesquisa.</p>
                
                <img id="projetoIMG" src="https://static.wixstatic.com/media/8b4bc0_d7649933a9a147f7a435d9968b618197~mv2.jpg/v1/fill/w_429,h_295,al_c,q_80,usm_0.66_1.00_0.01/ebacontemporanea.webp"></img>
            </div>
                <br/>
                <p className="p_normal">At o momento, o grupo formado por estudantes de graduao 
                    da Escola de Belas Artes da UFRJ fez um primeiro mapeamento, 
                    compreendendo 208 artistas e 12 coletivos/eventos/espaos autnomos, 
                    da passagem pelos diversos cursos de graduao  ps-graduao 
                    (mestrado, doutorado e ps-doutorado) no Programa de Ps-graduao 
                    em Artes Visuais - PPGAV/UFRJ. Esse quantitativo representa um 
                    recorte metodolgico inicial, e tender a crescer, a partir do 
                    desenvolvimento de etapas futuras da pesquisa.</p>

                <br/>

                <p className="p_normal">Assim, o projeto EBA Contempornea objetiva dar maior 
                    visibilidade a artistas visuais que se formaram na Escola de 
                    Belas Artes e a certos aspectos da histria recente da instituio, 
                    sem perder de vista as contradies, conflitos e problemticas 
                    inerentes a esse processo. No pretende instituir um tom celebratrio, 
                    mas articular mltiplas vozes, que constituem fragmentos dessa histria, 
                    do ponto de vista de artistas. Ao mesmo tempo, reforar um carter plural 
                    da Escola, apresentando a produo contempornea como parte significativa 
                    desse contexto, junto a outras formas de atuao que caracterizam a EBA.</p>
                
            </section>
        </>
    ) 
};